import * as React from "react";
import menu from "./menu";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { Button, CardActionArea, CardActions } from "@mui/material";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";

const style = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4
};

function menus2(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  for (let i = 1; i < values.length; i++) {
    items.push(
      <li>
        {keys[i]}:{values[i]}
      </li>
    );
  }
  return <ul>{items}</ul>;
}

function menus(index) {
  const items = [];
  const keys = Object.keys(menu[index]);
  const values = Object.values(menu[index]);
  return (
    <p>
      <font size="2" color="#a1a3a6">
        {values[1]}
      </font>
      <br />
      <font size="10">{values[2]}</font>
    </p>
  );
}

export default function MultiActionAreaCard(props) {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  return (
    <Card sx={{ maxWidth: 200 }}>
      <CardActionArea onClick={handleOpen}>
        <Modal
          open={open}
          onClose={handleClose}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <Box sx={style}>{menus2(1)}</Box>
        </Modal>
        <CardContent>{menus(1)}</CardContent>
      </CardActionArea>
    </Card>
  );
}
